<template>
  <div class="booking-form-wrapper flexbox p-1 p-t-3 pos-relative zindex-1 w-40 mobile-w-100">
    <div class="form-container mr-l-1 w-80 mobile-m-l-0 mobile-p-1 mobile-w-100 mobile-mr-l-0">
      <form method="post" v-on:submit="getSubRequest" action="#">
        <div class="form-header">
          <h5 class="wight-semibold text-white mr-0 font-semibold">Get The Modern Experience</h5>
          <h2
            class="title text-white mr-0 mr-t-sm mr-b-1 font-bold font-xl mobile-font"
          >Book a ticket now</h2>
        </div>

        <div class="form-body">
          <div class="form-group">
            <label class="sr-only" id>Leaving From</label>
            <v-select
              v-model="formData.from"
              class="modern-selector"
              placeholder="Leaving From"
              label="town_name"
              :options="towns"
            ></v-select>
          </div>

          <div class="form-group">
            <label class="sr-only" id>Travelling To</label>
            <v-select
              v-model="formData.to"
              class="modern-selector"
              placeholder="Travelling To"
              label="town_name"
              :options="towns"
            ></v-select>
          </div>

          <div class="form-group">
            <label class="sr-only">Travelling On</label>
            <DatePicker
              v-model="travelDate"
              :min-date="new Date()"
              :max-date="maxTravelDate"
              :input-props="{
                class: 'vc-calendar-custom',
                placeholder: 'Travelling On',
                readonly: true
              }"
            />
          </div>

          <!-- Show form feedback here -->
          <div class="form-group form-message text-white text-center hidden">Enter some form feedback here or error message</div>
          <!-- Show form feedback here -->

          <!-- Submit Form -->
          <div class="form-group">
            <button class="btn btn-submit mr-t-sm">SEARCH AVAILABLE BUSES</button>
          </div>
          <!-- /Submit Form -->
        </div>
      </form>
    </div>
  </div>
</template>

<script>
/* Towns data, TODO: Fetch from API */
let towns = [
  { id: 1, town_name: "Nairobi" },
  { id: 2, town_name: "Mombasa" }
];

/* Max date, 4 days from today */
let getMaxTravelDate = () => {
  let today = new Date();
  let tomorrow = new Date(today);
  return tomorrow.setDate(tomorrow.getDate() + 4);
};

export default {
  name: "BookingForm",
  data() {
    return {
      travelDate: null,
      maxTravelDate: getMaxTravelDate(),
      towns: towns,
      formData: {
        to: "",
        from: ""
      }
    };
  },
  methods: {
    getSubRequest: function(event) {
      event.preventDefault();
      this.$router.push({ name: "Buses", params: { userId: "123" } });
    }
  },
  mounted() {
    console.log("Loader");
  }
};
</script>
