<template>
  <div class="page-wrapper buses-page">
    <!-- App Header -->
    <AppHeader layoutType="layout-2"></AppHeader>

    <section class="viewport-h-min-100">
      <!-- Include AppTripFilter Componenet -->
      <AppTripFilter v-if="showAppTripFilter" />

      <!-- Include AppLoader (Show Progress On Page) -->
      <AppLoader v-if="showAppLoader" />

      <!-- Include AppBusResults Componenet  -->
      <AppBusResults v-if="showAppBusResults" @busSelected="busSelectedEvent" />

      <!-- Include AppBusSeats Componenet  -->
      <AppBusSeats v-if="showAppBusSeats" @busSeatSelected="busSeatSelectedEvent" />

      <!--Include AppPassangerForm Componenet -->
      <AppPassangerForm
        v-if="showAppPassangerForm"
        @passangerFormSubmitted="passangerFormSubmittedEvent"
      ></AppPassangerForm>

      <!-- Include AppBusSeats Componenet  -->
      <AppTripReview v-if="showAppTripReview" @tripReviewConfirmed="tripReviewEvent" />

      <!-- Include AppPayment Componenet  -->
      <AppPayment v-if="showAppPayment" />
    </section>

    <!-- App Footer -->
    <AppFooter />

    <!-- App Footer -->
    <AppModal v-if="showModal" @close="showModal = false" @demoModalContinue="modalClosedEvent"></AppModal>
  </div>
</template>

<script>
//AppHeader component
import AppHeader from "@/components/AppHeader.vue";

//AppTripFilter component
import AppTripFilter from "@/components/AppTripFilter.vue";

//AppTLoader component
import AppLoader from "@/components/AppLoader.vue";

//AppBusResults Componenet
import AppBusResults from "@/components/AppBusResults.vue";

//AppBusSeats Componenet
import AppBusSeats from "@/components/AppBusSeats.vue";

//AppPassangerForm component
import AppPassangerForm from "@/components/AppPassangerForm.vue";

//AppTripReview component
import AppTripReview from "@/components/AppTripReview.vue";

//AppPayment component
import AppPayment from "@/components/AppPayment.vue";

//AppFooter component
import AppFooter from "@/components/AppFooter.vue";

//AppModal component
import AppModal from "@/components/AppModal.vue";

export default {
  name: "Buses",
  components: {
    AppHeader,
    AppTripFilter,
    AppLoader,
    AppBusResults,
    AppBusSeats,
    AppPassangerForm,
    AppTripReview,
    AppPayment,
    AppFooter,
    AppModal
  },
  data() {
    return {
      showAppTripFilter: true,
      showAppLoader: false,
      showAppBusResults: true,
      showAppBusSeats: false,
      showAppPassangerForm: false,
      showAppTripReview: false,
      showAppPayment: false,
      showModal: false,
      seats: null
    };
  },
  methods: {
    busSelectedEvent: function() {
      let self = this;

      //scroll to top
      window.scrollTo(0, 0);

      //hide the filter component
      self.showAppTripFilter = false;
      //hide the bus component
      self.showAppBusResults = false;
      //show the bus component
      self.showAppBusSeats = true;
    },
    busSeatSelectedEvent: function() {
      let self = this;

      //scroll to top
      window.scrollTo(0, 0);

      //show modal componenet
      self.showModal = true;
    },
    modalClosedEvent: function() {
      let self = this;

      //scroll to top
      window.scrollTo(0, 0);

      //hide bus seat component
      self.showAppBusSeats = false;

      //hide modal componenet
      self.showModal = false;

      //show passanger form componenet
      self.showAppPassangerForm = true;
    },
    passangerFormSubmittedEvent: function() {
      let self = this;

      //scroll to top
      window.scrollTo(0, 0);

      //hide passanger details
      self.showAppPassangerForm = false;

      //show review
      self.showAppTripReview = true;
    },
    tripReviewEvent: function() {
      let self = this;

      //scroll to top
      window.scrollTo(0, 0);

      //hide passanger form component
      self.showAppTripReview = false;

      //show payemnet component
      self.showAppPayment = true;
    },
    filterRequest: function(event) {
      event.preventDefault();
    },
    seatSelected: function(seat_) {
      console.log(seat_);
    },
    getSelectedBusSeats: function() {
      this.showAppBusSeats = true;
    }
  },
  mounted() {
    let self = this;
    
    //scroll to top
    window.scrollTo(0, 0);

    //ProgressBar Demo
    self.showProgressBar = true;
    setTimeout(() => {
      self.showProgressBar = false;
    }, 2000);
  }
};
</script>
