<template>
  <div class>
    <div class="seat-labels">
      <div class="container">
        <div class="flexbox align-items-center">
          <img src="@/assets/icons/booked.svg" alt />
          <span>Booked</span>
        </div>
        <div class="flexbox align-items-center">
          <img src="@/assets/icons/available.svg" alt />
          <span>Available</span>
        </div>
        <div class="flexbox align-items-center">
          <img src="@/assets/icons/selected.svg" alt />
          <span>Selected</span>
        </div>
      </div>
    </div>

    <div class="container p-1 pos-relative">
      <!-- Loader -->
      <div
        class="bus-progress-loader flexbox align-items-center justify-center zindex-10"
        v-if="!busSeatLoadingComplete"
      >
        <div class="text-primary">
          <i class="fas fa-spinner fa-pulse fa-5x"></i>
        </div>
      </div>

      <div
        class="title-wrapper with-icon pos-relative flexbox justify-center align-items-center p-t-xs p-b-xs mr-t-2 mr-b-2"
      >
        <span class="p-l-2 p-r-2 mobile-p-l-0 mobile-p-r-0 align-items-center zindex-5">
          <img src="@/assets/icons/bus-icon-blue.svg" alt="Book Bus" />
          <h3 class="title font-h-2 mobile-font-sm">Select your seats</h3>
        </span>
      </div>

      <div class="row seat-layout-row p-t-1 pos-relative">
        <div class="bus-seat-wrapper for-desktop p-b-2">
          <table border="0" cellpadding="2" cellspacing="1" class="bus-body w-100">
            <tr v-for="row_ in  activeTableCols" :key="row_">
              <td v-for="col_ in activeTableRows" :key="col_">
                <div v-for="seat_ in seats" :key="seat_.id">
                  <div class="seat-item" v-if="row_ == seat_.seat_col && col_ == seat_.seat_row">
                    <input
                      type="checkbox"
                      :id="`seat-${seat_.seat_col}-${seat_.seat_row}`"
                      class="seat-checkbox"
                      @change="seatSelected(seat_)"
                      v-bind:disabled="seat_.seat_number == 'Dr'"
                    />
                    <label
                      :for="`seat-${seat_.seat_col}-${seat_.seat_row}`"
                      :title="`${seat_.seat_number} | ${seat_.seat_type} | ${seat_.price.price_kes_amount}`"
                    >
                      <span class="seat-text">{{seat_.seat_number}}</span>
                      <span class="armrest-right arm"></span>
                      <span class="armrest-left arm"></span>
                      <span class="backrest"></span>
                    </label>
                  </div>
                </div>
              </td>
            </tr>
          </table>
        </div>

        <div class="bus-seat-wrapper for-mobile p-b-2">
          <table border="0" cellpadding="2" cellspacing="1" class="bus-body w-100">
            <tr v-for="row_ in activeTableRows" :key="row_">
              <td v-for="col_ in activeTableCols" :key="col_">
                <div v-for="seat_ in seats" :key="seat_.id">
                  <div class="seat-item" v-if="row_ == seat_.seat_row && col_ == seat_.seat_col">
                    <input
                      type="checkbox"
                      :id="`seat-mobile-${seat_.seat_col}-${seat_.seat_row}`"
                      class="seat-checkbox"
                      @change="seatSelected(seat_)"
                      v-bind:disabled="seat_.seat_number == 'Dr'"
                    />
                    <label :for="`seat-mobile-${seat_.seat_col}-${seat_.seat_row}`">
                      <span class="seat-text">{{seat_.seat_number}}</span>
                      <span class="armrest-right arm"></span>
                      <span class="armrest-left arm"></span>
                      <span class="backrest"></span>
                    </label>
                  </div>
                </div>
              </td>
            </tr>
          </table>
        </div>

        <div class="current-trip-details-panel w-33 mobile-w-100">
          <div class="inline-form-wrapper">
            <form
              method="post"
              class="inline-form"
              @submit.prevent="$emit('busSeatSelected')"
              action="#"
            >
              <div class="form-group p-0 mr-b-1">
                <label class id="boading-point">Boarding Point</label>
                <v-select
                  class="modern-selector is-inline"
                  placeholder="Select Boading Point"
                  label="town_name"
                  id="boading-point"
                  :options="bookingPoints"
                ></v-select>
              </div>

              <div class="form-group p-0 mr-b-1">
                <label class id="dropping-point">Dropping Point</label>
                <v-select
                  class="modern-selector is-inline"
                  placeholder="Select Dropping Point"
                  label="town_name"
                  id="dropping-point"
                  :options="droppingPoints"
                ></v-select>
              </div>

              <div class="form-group pricing-total-wrapper">
                <div
                  class="row-item selected-seat-wrapper flexbox justify-between align-items-center p-b-1"
                >
                  <span class="label font-semibold">Selected Seats</span>
                  <span class="value font-bold">4, 5, 6</span>
                </div>
                <div
                  class="row-item total-amount-wrapper flexbox justify-between align-items-center p-b-1"
                >
                  <span class="label font-semibold">Total Amount</span>
                  <span class="value font-bold">KES 4,500</span>
                </div>
              </div>

              <!-- Show form feedback here -->
              <div
                class="form-group form-message text-center bg-white hidden"
              >Enter some form feedback here or error message</div>
              <!-- Show form feedback here -->

              <!-- Submit Form -->
              <div class="form-group">
                <button class="btn" id="continue-btn">CONTINUE</button>
              </div>
              <!-- /Submit Form -->
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* Towns data, TODO: Fetch from API */
let booking_point = [{ id: 1, town_name: "Accra Road" }];

let dropping_point = [{ id: 1, town_name: "Total Moi Avenue" }];

export default {
  name: "AppBusSeats",
  data() {
    return {
      busSeatLoadingComplete: false,
      bookingPoints: booking_point,
      droppingPoints: dropping_point,
      activeTableCols: 5,
      activeTableRows: 3,
      buseData: {},
      seats: null
    };
  },
  methods: {
    seatSelected: function(seat_) {
      console.log(seat_);
    },
    getBusDetailsRequest: function() {
      let self = this;
      var requestOptions = {
        method: "GET",
        redirect: "follow"
      };

      fetch("https://modern-html.herokuapp.com/getBusInfo.php", requestOptions)
        .then(response => {
          return response.json();
        })
        .then(data => {
          let _busData = data.bus_type;
          self.seats = data.seats;
          self.buseData = data.bus_type;
          self.activeTableCols = _busData.bus_type_cols;
          self.activeTableRows = _busData.bus_type_rows;
          self.busSeatLoadingComplete = true;
        })
        .catch(error => {
          alert(`Server error ${error}`);
        });
    }
  },
  mounted() {
    this.getBusDetailsRequest();
  }
};
</script>

